import { FirstRunPage } from './../pages/index';
import { SmartAudioProvider } from './../providers/smart-audio/smart-audio';
import { HomePage } from './../pages/home/home';
import { WelcomePage } from './../pages/welcome/welcome';
import { Component, ViewChild } from '@angular/core';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { TranslateService } from '@ngx-translate/core';
import { Config, Nav, Platform, ModalController } from 'ionic-angular';
import { timer } from 'rxjs/observable/timer';
import { MenuController } from 'ionic-angular';
import { NativeAudio } from '@ionic-native/native-audio';

import { Settings } from '../providers';
import { SplashscreenPage } from '../pages/splashscreen/splashscreen';

@Component({
  template: `<ion-menu [content]="content">
    <ion-header>
      <ion-toolbar>
        <ion-title>Menu</ion-title>
      </ion-toolbar>
    </ion-header>

    <ion-content>
      <ion-list>
        <button class="myboutton" menuClose ion-item *ngFor="let p of pages" (click)="openPage(p)">
          {{p.title}}
        </button>
      </ion-list>
    </ion-content>

  </ion-menu>
  <ion-nav #content [root]="rootPage"></ion-nav>`
})
export class MyApp {
  rootPage:any = WelcomePage;
  FirstRunPage:any = SplashscreenPage;
  showSplash = true; // <-- show animation


  @ViewChild(Nav) nav: Nav;

  pages: any[] = [
    { title: 'Liste des expositions', component: HomePage },
    { title: 'Ma liste de favoris', component: 'MyFavoritesPage' },
    { title: 'Mon profil', component: 'MyProfilePage' },
    { title: 'FeedBack', component: 'FeedbackPage' },
  ]
  constructor(private translate: TranslateService, platform: Platform, settings: Settings, private config: Config, private statusBar: StatusBar, splashScreen: SplashScreen,public menuCtrl: MenuController, smartAudio: SmartAudioProvider,modalCtrl: ModalController,private nativeAudio: NativeAudio) {
    platform.ready().then(() => {
      this.menuCtrl.enable(false);
      menuCtrl.swipeEnable(false);
      // smartAudio.play('opening');
      this.statusBar.styleDefault();
      splashScreen.hide();  // <-- hide static image
      timer(3000).subscribe(() => this.showSplash = false) // <-- hide animation after 3s
      // this.splashScreen.hide();  // <-- hide static image
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      this.statusBar.styleDefault();
      smartAudio.preload('opening', 'assets/img/isiko-opening.mp3');
      smartAudio.play('opening');
      
    });
    
    this.initTranslate();
  }

  ionViewDidLoad() {
    
    this.menuCtrl.swipeEnable(false);
    this.menuCtrl.enable(false);
    
  }

  


  initTranslate() {
    // Set the default language for translation strings, and the current language.
    this.translate.setDefaultLang('fr');
    const browserLang = this.translate.getBrowserLang();

    if (browserLang) {
      if (browserLang === 'zh') {
        const browserCultureLang = this.translate.getBrowserCultureLang();

        if (browserCultureLang.match(/-CN|CHS|Hans/i)) {
          this.translate.use('zh-cmn-Hans');
        } else if (browserCultureLang.match(/-TW|CHT|Hant/i)) {
          this.translate.use('zh-cmn-Hant');
        }
      } else {
        this.translate.use(this.translate.getBrowserLang());
      }
    } else {
      this.translate.use('fr'); // Set your language here
    }

    this.translate.get(['BACK_BUTTON_TEXT']).subscribe(values => {
      this.config.set('ios', 'backButtonText', values.BACK_BUTTON_TEXT);
    });
  }


  openPage(page) {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
    this.menuCtrl.swipeEnable(false);
    this.nav.setRoot(page.component);
  }
}



